import React, { useContext, useEffect, useCallback } from "react";
import { useHistory, Link } from "react-router-dom";
import logo from "../image/navbaricon1.png";
import { AuthContext } from "../Auth/AuthContext";
import axios from "axios";

const Navbar = () => {
  const { token, setToken, setGoogleId } = useContext(AuthContext);
  const history = useHistory();

  // Gunakan useCallback agar tidak tertrigger ulang
  const handleCredentialResponse = useCallback(async (response) => {
    console.log("[Google] Credential received:", response.credential);

    try {
      const tokenId = response.credential;
      window.localStorage.setItem("token", tokenId);

      const serverRes = await axios.post(
        `${"http://localhost:5000"}/patients/google-login/`,
        { tokenId }
      );

      if (serverRes) {
        setToken(tokenId);
        setGoogleId(serverRes.data.googleId || "");

        if (serverRes.data.phoneNumberExists === true) {
          history.push("/patient");
        } else {
          history.push("/patient/update-phone");
        }
      } else {
        throw new Error("Server didn't respond");
      }
    } catch (err) {
      console.error(`[Google] Error in credential response: ${err}`);
    }
  }, [history, setGoogleId, setToken]);

  useEffect(() => {
    if (window.google && window.google.accounts) {
      window.google.accounts.id.initialize({
        client_id: "419807594029-4lbtpnqflhtahs3j6pn99pivhri63953.apps.googleusercontent.com",
        callback: handleCredentialResponse,
      });

      window.google.accounts.id.renderButton(
        document.getElementById("google-signin-button"),
        {
          theme: "outline",
          size: "large",
          width: 200,
        }
      );
    }
  }, [handleCredentialResponse]);

  const signOutGoogle = () => {
    // GIS tidak menyimpan session, jadi cukup hapus token
    window.localStorage.removeItem("token");
    window.localStorage.removeItem("googleId");
    setToken(null);
    setGoogleId(null);
    history.push("/");
  };

  return (
    <nav className="navbar navbar-dark bg-dark navbar-expand-lg pl-4 pr-4 w-100">
      <Link to="/" className="navbar-brand">
        <img
          src={logo}
          alt=""
          width="30"
          height="24"
          className="d-inline-block align-top mr-2 mt-1"
        />
        Hospital Management System
      </Link>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#collapsibleNavbar"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="collapsibleNavbar">
        <ul className="navbar-nav ml-auto text-light bg-dark">
          <li className="navbar-item" style={{ textAlign: "right" }}>
            {!token && (
              <div id="google-signin-button" className="mr-2"></div>
            )}
            {token && (
              <button
                type="button"
                className="btn btn-outline-primary"
                onClick={signOutGoogle}
              >
                Logout
              </button>
            )}
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
